<template>
  <div
    v-if="api_data"
    id="responses"
    class="table-container box"
  >
    <table 
      class="table is-striped is-hoverable is-fullwidth" 
      style="border: 1px solid #dbdbdb;"
    >
      <caption class="mb-0 p-2">
        <span class="title">{{ api_data.title }}</span><br><span class="subtitle">Registo de participantes
        </span>
      </caption>
      <thead>
        <tr>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Participante
          </th>
          <th
            v-if="!event"
            style="text-align: center; vertical-align: middle;"
          >
            Evento
          </th>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Estado
          </th>
        </tr>
        <tr>
          <th class="p-0">
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="authorSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por autor"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
          <th 
            v-if="!event" 
            class="p-0"
          >
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="eventSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por evento"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
          <th class="p-0">
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="statusSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por estado (Validada / Presente)"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(response, responseIndex) in filteredResponses()"
          :key="responseIndex"
        >
          <td class="author">
            <span
              v-if="response.author"
              :href="'/profile/'+response.answerer"
            >{{ response.author }}</span>
            <template v-else>
              An칩nimo
            </template><span
              :title="new Date(response.created_at).toLocaleString()"
            >, h치 {{ timeSince(response.created_at) }}</span><span
              v-if="response.created_at != response.updated_at"
              :class="response.created_at != response.updated_at ? 'edited' : ''"
              :title="'Editado por ' + (!response.editor ? 'An칩nimo' : response.editor) + ' h치 ' + timeSince(response.updated_at) + ' (' + new Date(response.updated_at).toLocaleString() + ')'"
            >*</span><br>
            <a
              :href="'/forms/respond/' + response.id + '?event=' + response.event"
            >Editar Resposta</a>
          </td>
          <td v-if="!event">
            {{ response.eventName }}
          </td>
          <td
            align="center"
            style="vertical-align: middle;"
          >
            <span
              v-if="loading_status[response.id] === 'error'"
              class="is-danger connection-error"
            >
              Houve um erro de conex칚o. Por favor recarregue a p치gina.
            </span>
            <button
              v-else-if="answerStatuses[response.id] == 2"
              :class="{'is-loading is-warning' : loading_status[response.id] === 'loading', 'is-success' : loading_status[response.id] !== 'loading'}"
              type="button"
              class="button"
              @click="answerStatuses[response.id] = 4; changeStatus(response.id, answerStatuses[response.id])"
            >
              Efetuar registo de participante
            </button>
            <button
              v-else-if="answerStatuses[response.id] == 4"
              :class="{'is-loading is-success' : loading_status[response.id] === 'loading', 'is-warning' : loading_status[response.id] !== 'loading'}"
              type="button"
              class="button"
              @click="answerStatuses[response.id] = 2; changeStatus(response.id, answerStatuses[response.id])"
            >
              Cancelar registo de participante
            </button>
            <span v-else>
              Inscri칞칚o n칚o validada
            </span>
            <!-- <div 
                class="select is-rounded is-fullwidth" 
                :class="{'is-loading' : loading_status[response.id] === 'loading', 'is-danger connection-error' : loading_status[response.id] === 'error'}"
              >
                <select
                  v-model="answerStatuses[response.id]"
                  style="border-width: 2.5px;"
                  :style="{borderColor: loading_status[response.id] !== 'error' ? statuses[answerStatuses[response.id]].color : ''}"
                  @change="changeStatus(response.id, answerStatuses[response.id])"
                >
                  <option 
                    v-if="loading_status[response.id] === 'error'" 
                    value="error" 
                    unselectable
                  >
                    Erro de conex칚o
                  </option>
                  <option
                    v-for="(status, key) in statuses"
                    :key="key"
                    :value="key"
                  >
                    {{ status.name }}
                  </option>
                </select>
              </div> -->
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
import { decodeSections, getCookie, removeExpositionSections, timeSince, stringIn } from "@/common";
export default {
  name: "ViewAllResponses",
  components,
  data() {
    return {
      api_data: undefined,
      form_responses: [],
      displayed_sections: [],
      event: undefined,
      user: undefined,
      statuses: {},
      authorSearch: "",
      eventSearch: "",
      statusSearch: "",
      answerStatuses: [],
      interval : undefined,
      loading_status : {}
    };
  },
  created() {
    const form = API.get("form/".concat(this.$route.params.id))
      .then((response) => 
        this.initializeData(response)
      )
    var queryString = "formAnswer/?form=".concat(this.$route.params.id);
    if (this.$route.query.event !== undefined) {
      queryString = queryString.concat("&event=").concat(this.$route.query.event)
      API.get("event/".concat(this.$route.query.event))
       .then(response => this.event = response.data.name)
    }
    // if (this.$route.query.user !== undefined) {
    //   queryString = queryString.concat("&user=").concat(this.$route.query.user)
    //   API.get("user/".concat(this.$route.query.user))
    //    .then(response => this.user = response.data.username);
    // }
    API.get(queryString)
      .then(async (response) => {
          await form;
          this.initializeAnswers(response)
        }
      );

    API.get("answerStatus/")
      .then((response) => {
        this.statuses = response.data;
      });
    this.getStatuses();
    //this.interval = setInterval(() => this.getStatuses(), 10000);
  },
  methods: {
    getStatuses() {
      API.get("formAnswerStatus/")
      .then((response) => {
        this.answerStatuses = response.data;
      });
    },
    changeStatus(answer, status) {
      this.loading_status[answer] = 'loading';
      setTimeout(this.patchStatus.bind(this, answer, status), 0);
    },
    patchStatus(answer, status) {
      const options = {
        method: "patch",
        url: "/formAnswerStatus/" + answer + "/",
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: {
          "status": status
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options)
        .then(() => this.loading_status[answer] = undefined)
        .catch((error) => {
          console.log(error); 
          this.loading_status[answer] = 'error';
          this.answerStatuses[answer] = 'error';
        });
    },
    initializeData(response) {
      this.api_data = removeExpositionSections(response.data);
      for (var i = 0; i < this.api_data.form.sections.length; /*do nothing*/) {
        if (this.api_data.form.sections[i].questions.length > 0)
          this.displayed_sections[i++] = true;
        else
          this.api_data.form.sections.splice(i, 1);
      }
    },
    initializeAnswers(response) {
      this.form_responses = decodeSections(response.data, this.api_data);
    },
    allFalse() {
      for (var i = 0; i < this.displayed_sections.length; i++)
        if (this.displayed_sections[i] == true)
          return false;
      return true;
    },
    filteredResponses() {
      var result = this.form_responses;
      if (this.authorSearch != "")
        result = result.filter(x => stringIn(x.author === undefined ? "An칩nimo" : x.author, this.authorSearch));
      if (this.eventSearch != "")
        result = result.filter(x => {return x.eventName ? stringIn(x.eventName, this.eventSearch) : false});
      if (this.statusSearch != "")
        result = result.filter(x => stringIn(this.statuses[this.answerStatuses[x.id]].name, this.statusSearch));
      for (var sectionIndex = 0; sectionIndex < this.api_data.form.sections.length; sectionIndex++) {
        if (this.displayed_sections[sectionIndex]) {
          var section = this.api_data.form.sections[sectionIndex];
          for (var questionIndex = 0; questionIndex < section.questions.length; questionIndex++) {
            var question = section.questions[questionIndex]
            if (question.filter && question.filter != "")
              result = result.filter(x => stringIn(String(x.content.sections[sectionIndex][questionIndex].answer), question.filter));
          }
        }
      }
      return result;
    },
    timeSince: timeSince
  }
};
</script>
